import React, { useEffect, useRef } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig, general, skills, techs } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';

const StyledAboutSection = styled.section`
  max-width: 900px;

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;
const StyledText = styled.div`
  ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '▹';
        position: absolute;
        left: 0;
        color: ${props => props.theme.higlight};
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
`;
const StyledPic = styled.div`
  position: relative;
  max-width: 300px;

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    ${({ theme }) => theme.mixins.boxShadow};
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);
    background-color: ${props => props.theme.higlight};

    &:hover,
    &:focus {
      background: transparent;
      outline: 0;

      &:after {
        top: 15px;
        left: 15px;
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    .img {
      position: relative;
      border-radius: var(--border-radius);
      // mix-blend-mode: multiply;
      // filter: grayscale(100%) contrast(1);
      transition: var(--transition);
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    &:before {
      top: 0;
      left: 0;
      background-color: ${props => props.theme.navy};
      mix-blend-mode: screen;
    }

    &:after {
      border: 2px solid ${props => props.theme.higlight};
      top: 20px;
      left: 20px;
      z-index: -1;
    }
  }
`;

const About = () => {
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  return (
    <div>
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">About Me</h2>
      <div className="inner">
        <StyledText>
          <div>
            <p>
            I'm Zarif, currently working as a Machine Learning Engineer, focused on solving some pieces of the <a href='https://en.wikipedia.org/wiki/Dark_store' target="_blank" rel="noreferrer"> <i>'Dark Store'</i> </a> puzzle and also scaling that to build large scale ML systems which will be able to facilitate millions of people at <a href="https://chaldal.tech/" target="_blank" rel="noreferrer">Chaldal Ltd.</a>!
            </p>
            <p>
            I love working on real world problems that have an impact, and obsess myself till I find a solution. My research interest lies in making machines
            <b><i class="tooltip" data-tooltip="Computer Vision!">see</i></b>
            and
            <b><i class="tooltip" data-tooltip="Natural Language Processing!">comprehend.</i></b>
            I don't have any prejudice for any programming language or framework. I believe in using the right tool for the right job.

            My research and software engineering life has a quite blurry line. I try to put my research skill to solve a problem, and my software engineering skill to ship it to production.
            I also regularly participate in hackathons (I won <a href="https://cse.buet.ac.bd/home/news_detail/157" target="_blank" rel="noreferrer">Bangladesh's first NLP hackathon</a>!) and machine learning competitions.
            </p>

            <p>
              In an alternate universe, I would have been a political scientist. 
              The mysteries of history and geopolitics have always captivated me. 
              In my free time, I ponder upon the vastness of creation and the greatness of the creator.
              I am a foodie and love the silence of the mountains. 
              Planning to travel the whole world one place at a time!
            </p>

            <p>
              <b>
                <i> Fun fact :</i></b> My Erdős number is 
                <b><i class="tooltip" data-tooltip="Paul Erdős - László A. Székely - Tandy J. Warnow - Md. Shamsuzzoha Bayzid - Me">4.</i></b>
            </p>
            
            
          </div>

          {/* <div>
            <br />
            <div>
              <p>Here are a some skills about my job</p>
            </div>
            <ul className="skills-list">
              {skills && skills.map((skill, i) => <li key={i}>{skill}</li>)}
            </ul>
          </div>

          <div>
            <br />
            <div>
              <p>Here are a few technologies I've been working with recently</p>
            </div>
            <ul className="skills-list">
              {techs && techs.map((tech, i) => <li key={i}>{tech}</li>)}
            </ul>
          </div> */}
        </StyledText>

        <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/me.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
            />
          </div>
        </StyledPic>
      </div>
    </StyledAboutSection>
    {/* <p style={{align:'center'}}>
    <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/me.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
              align="center"
            />
          </div>
      </StyledPic>
      </p> */}
    </div>
  );
};

export default About;
