const StyledPic = styled.div`
  position: relative;
  max-width: 400px;
  margin: 0 auto 100px;

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    ${({ theme }) => theme.mixins.boxShadow};
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);

    &:hover,
    &:focus {
      background: transparent;
      outline: 0;

      &:after {
        top: 15px;
        left: 15px;
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    .img {
      position: relative;
      border-radius: var(--border-radius);
    }
  }
`;

const StyledContactSection = styled.section`
  max-width: 650px;
  margin: 0 auto 100px;
  text-align: center;

  @media (max-width: 768px) {
    margin: 0 auto 50px;
  }

  .overline {
    display: block;
    margin-bottom: 20px;
    color: ${props => props.theme.higlight};
    font-family: var(--font-mono);
    font-size: var(--fz-md);
    font-weight: 400;

    &:before {
      bottom: 0;
      font-size: var(--fz-sm);
    }

    &:after {
      display: none;
    }
  }

  .title {
    font-size: clamp(40px, 5vw, 60px);
  }

  .email-link {
    ${({ theme }) => theme.mixins.bigButton};
    margin-top: 50px;
    margin-bottom: 50px;
  }
`;

const Research = () => {
  
  const revealContainer = useRef(null);
  const prefersReducedMotion = usePrefersReducedMotion();

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  return (
    <StyledContactSection id="research" ref={revealContainer}>
      <h2 className="numbered-heading overline">A dive into my research!</h2>

      <h2 className="title">Research</h2>

      <StyledPic>
          <div className="wrapper">
            <StaticImage
              className="img"
              src="../../images/research_interest.png"
              // width={500}
              // quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
            />
          </div>
        </StyledPic>

      <p>
      My research has mostly been focused on <b><i>Computer Vision</i></b> so far. 
      Recently I've also been exploring <b><i>Natual Language Processing</i></b>, <b><i>Reinforcement Learning</i></b> and large scale machine learning systems in general as part of my work at Chaldal.
      To know more about my research, check <a href="/research">here</a>!
      </p>

      <a className="email-link" href='/research'>
        Explore My Research!
      </a>
    </StyledContactSection>
  );
};

export default Research;
